<template>

<div>
    <SingleImage v-if="selectedImageId" :id="selectedImageId" @close="closeImageModal" :images="images" />
    <div class="p-10 grid grid-cols-1 sm:grid-cols-1 md:grid-cols-3 lg:grid-cols-3 xl:grid-cols-3 gap-5" >
    <ImageCard v-for="image in images" :key="image.id" :image="image" @click=showImageModal(image.id)  />
</div>
    
</div>
</template>

<script>
import ImageCard from './ImageCard.vue'
import SingleImage from './SingleImage.vue';

export default {
    name:'ImageBoard',

    data(){
        return {
            selectedImageId: location.pathname.slice(1),
        
        }
       
    },

    components:{
        ImageCard,
        SingleImage

    },

    methods:{
        showImageModal(id) {
            window.addEventListener("popstate", ()=>{
                this.selectedImageId=location.pathname.slice(1)
            });
            this.selectedImageId = id;
            history.pushState({}, "", `/${id}`);
        },
        closeImageModal(invaled = false) {
            this.selectedImageId = null;
            if (invaled) {
                history.replaceState({}, "", "/");
            } else {
                history.pushState({}, "", "/");
            }
        },

    },

    
    
     

    props:{
        images:Array,
        
    },

    
   

}

</script>