<template>

<div>
<div class="flex flex-col justify-center items-center mt-3">
    <p class="bg-green-700 text-bold text-xl py-2 px-10" v-if="comment">
    {{comment.msg}}
</p>
    <form class="bg-gray-700 p-2 m-2 rounded" method="POST" @submit.prevent="handleSubmit">
        <div class="flex ">
            <label class="flex-none "   for="username">User Name</label>
            <input class="grow bg-gray-300 rounded-l p-2 boreder-none text-gray-700 ml-2 mb-2" v-model="username" type="text" name="username">
            <button class="flex-none bg-gray-300 rounded-r text-gray-700 p-2 mb-2 hover:bg-gray-500 hover:text-gray-100 " type="submit">Submit</button>
        </div>
        <div class="textarea">
            <textarea v-model="content" name="content" cols="60" rows="3" class="p-2 m-2 text-gray-700" dir="auto"></textarea>
        </div>
    </form>
</div>

</div>
    
</template>


<script>
const  moment =require("moment");
export default{
    name:'CommentForm',

    data(){
    return {
        moment:moment,
        comment:"",
    
    }


    },

    props:{
    id:String,
},

methods:{
    handleSubmit() {

        const body = {
                username: this.username,
                content: this.content,
                imageid: this.id,
            };
  

            fetch("api/comment", {
                method:"POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify(body),
            })
                .then((res) => {
                    return res.json();
                })
                .then((data) => {
            this.comment=data;
                console.log(this.comment)
                });

                this.username="",
               this.content=""
   
        }

}
}
</script>