<template>
    <div v-if="show" class="modal-mask">
        
    <div class="relative mt-2 min-h-screen flex items-center justify-center py-2 px-4 sm:px-6 lg:px-8 bg-no-repeat bg-cover relative items-center z-40">
	<div class="absolute opacity-50 inset-0 z-0"></div>
	<div class="sm:max-w-lg w-full p-10 bg-white rounded-xl z-10">
        <div class="flex flex-row-reverse ">
        <button class="bg-gray-300 rounded text-xl px-2 " @click="$emit('close')" ><i class="fa fa-close" > </i> </button>
    </div>
		<div class="text-center">
			<h2 class="mt-5 text-3xl font-bold text-gray-900">
				Image Upload
			</h2>            
		</div>
        <form action="#" method="POST" @submit.prevent="SubmitData" id="myForm">
                    <div class="grid grid-cols-1 space-y-2">
                        <label for="username" class="text-sm font-bold text-gray-500 tracking-wide">User Name</label>
                        <input class="text-base p-2 border border-gray-300 rounded-lg focus:outline-none focus:border-indigo-500" v-model="username" type="text" placeholder="Please enter user name" name="username"/>
                        <label for="title" class="text-sm font-bold text-gray-500 tracking-wide">Title</label>
                        <input class="text-base p-2 border border-gray-300 rounded-lg focus:outline-none focus:border-indigo-500" type="text" v-model="title" placeholder="Please enter title" name="title"/>
                        <label for="description" class="text-sm font-bold text-gray-500 tracking-wide">Description</label>
                        <textarea class="text-base p-2 border border-gray-300 rounded-lg focus:outline-none focus:border-indigo-500" type="text" v-model="description" placeholder="Please enter description" name="description"></textarea>
                    </div>
                    <div class="grid grid-cols-1 space-y-2">
                                    <label class="text-sm font-bold text-gray-500 tracking-wide">Attach Document</label>
                        <div class="flex items-center justify-center w-full">
                            <label class="flex flex-col rounded-lg border-4 border-dashed w-full h-60 p-10 group text-center">
                                <div class="h-full w-full text-center flex flex-col items-center justify-center items-center  ">
                                    <svg xmlns="http://www.w3.org/2000/svg" class="w-10 h-10 text-blue-400 group-hover:text-blue-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M7 16a4 4 0 01-.88-7.903A5 5 0 1115.9 6L16 6a5 5 0 011 9.9M15 13l-3-3m0 0l-3 3m3-3v12" />
                                    </svg>
                                    <div class="flex flex-auto max-h-48 w-2/5 mx-auto -mt-10">
                                    <!-- <img class="has-mask h-36 object-center mb-5" src="https://img.freepik.com/free-vector/image-upload-concept-landing-page_52683-27130.jpg?size=338&ext=jpg" alt="freepik image"> -->
                                    </div>
                                    <p class="pointer-none mt-3 text-gray-500 "><span class="text-sm">Drag and drop</span> files here <br /> or select a file from your computer</p>
                                </div>
                                <input type="file" name="image_file" class="hidden" accept="image/*" @change="uploadImage" placeholder="select the file">
                            </label>
                        </div>
                    </div>
                            <p class="text-sm text-gray-500">
                                <span>File type:Only images</span>
                            </p>
                            <p :class="image.msg ? 'bg-green-600 text-gray-100 text-center p-2 m-2 text-bold text-lg':'' ">{{image.msg}}</p>
                    <div>
                        <button type="submit" name="submit" class="my-5 w-full flex justify-center bg-blue-500 text-gray-100 p-4  rounded-full tracking-wide
                                    font-semibold  focus:outline-none focus:shadow-outline hover:bg-blue-600 shadow-lg cursor-pointer transition ease-in duration-300">
                        Upload
                    </button>
                    </div>
        </form>
	</div>
</div>
</div>

</template>
<script>

    export default {
        name: "ImageUpload",

        data () {
            return {
                image_file:null,
                username:"",
                title:"",
                description:"",
                image:{},
            }
        },

        methods: {

            uploadImage(event) {
                console.log(event)

                this.image_file = event.target.files[0];
            },



            SubmitData() {

                const formData = new FormData();
                formData.append('username', this.username),
                formData.append('title', this.title),
                formData.append('description', this.description),
                formData.append("image_file",this.image_file);
                fetch("api/images",
                {
                    method: "POST",
                    body:formData
                })
                .then((res)=> {
                    if(res.ok){
                        return res.json();
                    }
               })
               .then((data)=>{
                
              this.image=data;
              console.log(this.image, "i am data from uploade image")
              
               })

            }
        },
        props:{
            show:Boolean,

        }
    }
</script>


