	.has-mask {
		position: absolute;
		clip: rect(10px, 150px, 130px, 10px);
	}

    .modal-mask{
        position: fixed;
        top:0;
        left:0;
        right:0; 
        background: rgba(0,0,0,0.6);
        overflow-wrap: normal;
    }

   
