<template>
  <div class="container max-auto py-4 md:container md:mx-auto">

    
  
    <HeaderVue @toggle-uploader="showUploader=true" :show-uploader="showUploader"/>
    <ImageUpload :show="showUploader" @close="showUploader=false"/>
    <ImageBoard :images="images"/>

    <div class="flex space-x-2 justify-center" v-show="!noMore">
  <button
    type="button"
    data-mdb-ripple="true"
    data-mdb-ripple-color="light"
    @click="loadMore"
    class="inline-block px-6 py-2.5 bg-blue-600 text-white font-medium text-xs leading-tight uppercase rounded shadow-md hover:bg-blue-700 hover:shadow-lg focus:bg-blue-700 focus:shadow-lg focus:outline-none focus:ring-0 active:bg-blue-800 active:shadow-lg transition duration-150 ease-in-out"
  >More Images</button>
</div>
  </div>
  
</template>

<script>

import HeaderVue from './components/HeaderVue.vue'
import ImageUpload from './components/ImageUpload.vue'
import ImageBoard from './components/ImageBoard.vue'

export default {

  name: 'App',

  components: {
    HeaderVue,
    ImageUpload,
    ImageBoard,

  }, 

  data(){
        return {
            images:[],
            showUploader:false,
            noMore:false
        }
    },

    

    methods:{

    async fetchImages(){
      const res= await fetch("/api/images")
      const data= await res.json()
      return data
    },

    loadMore(){
      const lastImage=this.images.slice(-1)
      const lastImageId=lastImage[0].id
    fetch(`/api/images/more?after=${lastImageId}`)
    .then((res)=>{
      if(res.ok){
        return res.json()
      }
    })
    .then((moreData)=>{
      console.log(moreData, "i am more data")
      const allImages=[...this.images, ...moreData]
      this.images=allImages
      if(moreData.length<6){
        this.noMore=true
      }
    });
  
  }
},

    async created(){
    this.images= await this.fetchImages()
   
    
  }

}
</script>
