<template>
<div class="text-gray-200 page-mask items-center h-full"  >
    <div class="modal-container">
        <h2 class="text-gray-300 text-3xl text-center rounded"> {{image.title}}</h2>
        <div class="flex justify-center items-center m-5 ">
            <i class="text-gray-300 text-7xl flex-none rounded p-1 px-2 fa-solid fa-angle-left"   @click="previousHandle"></i>
            <div class="grow flex justify-center items-center">
                <img class="w-3/5 h-1/2 object-cover"  :src="`${image.url}`" alt="">
            </div>
            <i class="text-gray-300 text-7xl flex-none rounded p-1 px-2 fa-solid fa-angle-right" @click="nextHandle"></i>
        </div>
        <div class="text-gray-700 font-2xl flex flex-row-reverse space-x-4 space-x-reverse ">
            <button class="bg-gray-200 rounded p-1 px-2" @click="$emit('close')" ><i class="fa fa-close" > </i> </button>
            <button  class="bg-gray-200 rounded p-1 px-2" @click="deleteHandle"><i class="fa fa-trash"></i></button>
        </div>
        <div >
            <div class="flex text-gray-200 p-1 justify-around rounded ">
            <p> {{image.username}}</p>
            <p> {{moment(`${ image.created_at}`).fromNow()}} </p>
        </div>
        <div class="flex justify-center mt-3">          
            <p class=" bg-gray-800 text-md text-gray-300 rounded p-2 w-3/5"> {{image.description}}</p>
        </div>
 
    </div>

    <CommentForm :id="image.id"/> 
    <CommentsVue :comments="comments"/>



</div>

</div>
    
</template>

<script>
const  moment =require("moment");

import CommentForm from './CommentForm.vue';
import CommentsVue from './CommentsVue.vue';


export default {
    name:"SingleImage",

    components:{
        CommentForm,
        CommentsVue
    },



    data(){
        return {
            image:{},
            comments: [],
            username: "",
            content: "",
            moment:moment,
            newImages:this.images,
            newIde:this.id,
        };
    },

    watch: {
        id: function () {
            this.selectedImageId = this.id;
            history.pushState({}, "", `/${this.id}`);
        },
    },


    mounted() {
        fetch("api/images/" + this.id)
            .then((res) => {
                if (res.ok) {
                    return res.json();
                }
            })
            .then((data) => {
                if (data) {
                    this.image = data;
                    console.log(data, "i am data for singimage modal")

                } else {
                    this.$emit("close", true);
                }
            });
        fetch("api/comments/" + this.id)
            .then((res) => {
                return res.json();
            })
            .then((data) => {
                this.comments = data;
            });
    },

    methods:{
        deleteHandle() {
            const body = {
                imageId: this.id,
            };

            console.log(body, "men body");

            fetch("api/image/delete", {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify(body),
            }).then((res) => {
                
                console.log(res, "ment response in delete");
                const index = this.images.findIndex((e) => e.id === this.id);
                this.newImages.splice(index, 1)
                const updatedImages=this.newImages
                console.log(updatedImages, "after slice")

                const updatedId = updatedImages[index % updatedImages.length].id;
                console.log(updatedId, "i am updated Id")
                window.location.href = updatedId;
                return res.json();
            });
        },

        previousHandle(){
            let idArray=[];
            idArray.push(this.images.map(e=>e.id));
            console.log(idArray[0], "i am idArray ")
            const currentImage=this.image.id
            let itemIndex=idArray[0].indexOf(currentImage) 
            console.log(itemIndex, "men items index in previous")           
            if(itemIndex !=-1 && itemIndex != 0){
                let previousItem=idArray[0][itemIndex-1]
                
                window.location.href = previousItem
            }
        },
        
        nextHandle(){
            let idArray=[];
            idArray.push(this.images.map(e=>e.id));
            const currentImage=this.image.id
            let itemIndex=idArray[0].indexOf(currentImage)            
            if(itemIndex >= 0 && itemIndex < idArray[0].length-1 ){
                let nextItem=idArray[0][itemIndex+1]
            
                window.location.href = nextItem
            }
        }
        
    },
    

    props:{
        images:Array,
        show:Boolean,
        id:String
    }
}
</script>

<style>
.page-mask{
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
    background-color: rgba(0, 0, 0, 0.9);
    overflow-y: scroll;
    display: flex;
    justify-content: center;
}

.modal-container{
    margin-top: 10rem;
    width: 80vw;

}


</style>